<template>
  <table class="kv-datagrid--body-table"
         cellspacing="0"
         cellpadding="0"
         border="0">
    <colgroup>
      <col v-for="(column, index) in leafColumns"
           :key="index"
           :style="column.colStyle">
    </colgroup>
    <template v-for="(row, rowIndex) in data">
      <table-row class="kv-datagrid--body-row"
                 :row="row"
                 :key="rowIndex"
                 :row-class="rowClass"
                 :row-style="rowStyle"
                 :row-index="rowIndex">
        <table-cell class="kv-datagrid--body-cell"
                    v-for="(column, cellIndex) in leafColumns"
                    :key="cellIndex"
                    :column="column"
                    :row="row"
                    :row-index="rowIndex"
                    :cell-class="cellClass"
                    :cell-style="cellStyle"
                    :column-index="cellIndex">
        </table-cell>
      </table-row>
      <table-row-expansion v-if="row.expand"
                           :key="`ex-${rowIndex}`"
                           :row="row"></table-row-expansion>
    </template>

  </table>
</template>

<script>
import TableRow from "./row";
import TableCell from "./cell";
import TableRowExpansion from "./expansion";

export default {
  components: { TableRow, TableCell, TableRowExpansion },
  inject: ['datagrid'],
  computed: {
    data() {
      return this.datagrid.dataSource
    },
    leafColumns() {
      return this.datagrid.leafColumns
    },
    rowClass() {
      return this.datagrid.rowClass
    },
    rowStyle() {
      return this.datagrid.rowStyle
    },
    cellClass() {
      return this.datagrid.cellClass
    },
    cellStyle() {
      return this.datagrid.cellStyle
    }
  },
  mounted() {
    this.$emit("on-after-render");
  }
};
</script>
